@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
  font-family: "poppins";
}
input[type=text]{
  z-index: 0;
}
.long {
  position: relative;
  /* background: #5e94ffa1; */
}
.long label,
.wrapper label {
  position: absolute;
  top: 7px;
  padding: 0px 10px;
  left: 20px;
}
.focus{
    position: absolute;
    top: -10px!important;
    z-index: 2;
    background: #fff;
    font-size: 13px;
    color: dodgerblue;
}

.valid{
    background: #fff !important;
    border: 3px solid dodgerblue !important;
}

.prodContainer{
   box-shadow: 10px 10px 10px rgba(119, 112, 112, 0.3);
   /* margin: 15px 5px; */
}

 .nextLink, .previousLink{
   border-radius: 5px !important;
   padding: 4px 8px !important;
 }
.pagination{
  align-items: center;
  display: flex;
  position: absolute;
  list-style: none;
  left: 20%;
  bottom: 10px;
  width: 40%;
}
.pagination a{  
  /* padding: 10px 12px; */
  padding: 8px 15px;
  margin: 8px;
  border-radius: 50%;
  outline: none;
}

.pagination a:hover{
  background: dodgerblue;
  color: #fff;
}
.product{
  height: 220px !important;
}
.activeLink{
    background: dodgerblue;
    color: #fff;
}