*{
  box-sizing: border-box;
}
.m-top{
  position: relative;
  margin-bottom: 50px !important;
}
.m-top h2{
  left: -50px;
  top: -90px;
  width: 100%;
}
.m-top h2, .m-top p{
  position: absolute;
}
.m-top p {
  left: 0px;
  top: -50px;
  width: 100%;
  /* margin-top: 50px !important;
  margin-bottom: 50px !important; */
  /* z-index: 1; */
}
.login {
  font-size: 14px !important;
  margin-top: 2px !important;
}

.signup {
  margin-top: 30px;
  font-size: 15px;
}
.justify > div {
  height: 50px !important;
}
label {
  z-index: 1.5;
}

input {
  z-index: 3;
}
input::placeholder {
  text-align: center;
  margin-top: 3px !important;
  font-size: 15px;
  transition: all.3s;
}

.focus {
  position: absolute;
  top: -10px !important;
  background: #fff;
  font-size: 13px;
  color: dodgerblue;
}


.valid{
  background: #fff !important;
  z-index: 1;
  border: 3px solid dodgerblue !important;
}
.long{
  position: relative;
}
.long label, .wrapper label{
  position: absolute;
  top: 7px;
  padding: 0px 10px;
  left: 20px;
  
}
