div.bank-row{
    display: block;
}
div.bank-row label , div.sub-bank-row label{
    display: block;
}
div.bank-row input {
    border: 1px solid grey;
    width: 100%;
    padding: 5px 15px;
    border-radius: 3px;
    height: 45px;

}
div.bank-row input::placeholder{
    text-align: left;
}
div#special-bank-row{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

div#button-bank-row{
    display: flex;
    margin-top: 10px;
    flex-direction: row;
    justify-content: space-between;
}
div#button-bank-row button{
    width: 30%;
    border: 1px solid grey;
    padding: 5px 15px;
    border-radius: 4px;
    height: 50px;
}

button.submit{
    background-color: dodgerblue;
    color: white;
}
button.reset{
    background-color: rgb(228, 228, 228);
}

@media only screen and (max-width:620px){
    div#button-bank-row button{
        width: 45%;
        padding: 5px 15px;
        height: 50px;
    }
}

@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1){
    div#special-bank-row{
        flex-direction: column;
    }

    div#button-bank-row button{
        width: 45%;
        padding: 5px 5px;
        height: 50px;
    }
}