*{
    box-sizing: border-box;
}
.sidebar-container{
width: 15%;
position: fixed;
height: 100%;
background-color: white;

}

.cross{
    display: none;
}


@media only screen and (max-width:620px){
    .sidebar-container{
        width: 10%;
        overflow: hidden;
        transition: 0.3s ease-out;
        z-index: 3;
    }
   .cross{
       display: inline;
       color: red;
       position: relative;
       top: -7px;
       left: 120px;
   }
    span.link-text{
        display: none;
    }
    h2.logo-name{
        display: none;
    }
    .logo-img{
        display: none;
    }
    .ico{
        font-size: 25px;
    }
    input[type=checkbox]:checked ~ .sidebar-container{
      width: 50%;
    }
}

@media only screen and (min-width:768px) and (max-width:1000px) and (-webkit-min-device-pixel-ratio: 1) {
    .sidebar-container{
        width: 8%;
        overflow: hidden;
        transition: 0.3s ease-out;
        z-index: 1;
        
    }
    span.link-text{
        display: none;
    }
    h2.logo-name{
        display: none;
    }
    .logo-img{
        display: none;
    }
    .cross{
        display: inline;
        color: red;
        position: relative;
        top: -10px;
        left: 150px;
    }

    input[type=checkbox]:checked ~ .sidebar-container{
        width: 30%;
      }

      .ico{
        font-size: 25px;
    }
}